export default {
	data() {
		return {
			goodsList: [
				{
					sp_images: "https://img14.360buyimg.com/n7/s600x600_jfs/t1/59141/5/9072/153303/5d6690d9Ea8a24391/b2d424722149a8ff.jpg!cc_230x230.jpg",
					sp_name: "HUAWEI WATCH GT运动版 黑色 华为手表 (两周续航+户外运动手表+实时心率+高清彩屏+",
					sp_pid: 2828,
					number: 1,
					selected: false
				}
			],
			shopList: [{
					id: 1,
					name: "冰鲜专卖店",
					img: "/static/img/shop/1.jpg"
				},
				{
					id: 2,
					name: "果蔬天下",
					img: "/static/img/shop/2.jpg"
				},
				{
					id: 3,
					name: "办公耗材用品店",
					img: "/static/img/shop/3.jpg"
				},
				{
					id: 4,
					name: "天天看好书",
					img: "/static/img/shop/4.jpg"
				}
			],
			headerTop: 0,
			//控制滑动效果
			typeClass: 'goods',
			subState: '',
			theIndex: null,
			oldIndex: null,
			isStop: false
		}
	},
	onPageScroll(e) {

	},
	//下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
	onPullDownRefresh() {
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	onLoad() {
		//兼容H5下排序栏位置










		this.getlike();
	},
	onShow() {
		this.getlike();
	},
	methods: {
		toGoods(id){
			console.log(id);
			uni.reLaunch({
			    url: '../../goods/goods?pid='+id
			});
		},
		changemylike() {
			var mylike;
			uni.getStorage({
				key: "mylike",
				success: res => {
					mylike = res.data;
					this.goodsList=mylike;
				},
				fail: res => {
					mylike = [{
						sp_images: "https://img14.360buyimg.com/n7/s600x600_jfs/t1/59141/5/9072/153303/5d6690d9Ea8a24391/b2d424722149a8ff.jpg!cc_230x230.jpg",
						sp_name: "HUAWEI WATCH GT运动版 黑色 华为手表 (两周续航+户外运动手表+实时心率+高清彩屏+",
						sp_pid: 2828,
						number: 1,
						selected: false
					}];
					this.goodsList=mylike;
				}
			})
		},
		getlike() {
			var token = "";
			uni.getStorage({
				key: "token",
				success: res => {
					token = res.data;
				}
			})
			if (token) {
				var data = {
					token,
					type: "production",
					action: "get"
				}
				uni.request({
					url: 'http://120.79.19.253:10086/Subscribe',
					data,
					success: (res) => {
						console.log(res.data, "hhhhhhhhhh");
						if (res.data.status == 1) {
							var data=res.data.sp_data;
							var images=[]
							for(var item of data)
							{
								item["number"]=1;
								item["selected"]=false;
								images.push(item.sp_images);
							};
							for(var i=0;i<images.length;i++)
							{
								var reg=/;/;
								if(reg.test(images[i]))
								{
									images[i]=images[i].split(";")[0];
								}
							};
							for(var item in data)
							{
								data[item].sp_images=images[item];
							}
							console.log(data);
							uni.setStorage({
								key:"mylike",
								data,
								success: (res) => {
									uni.showToast({
										title:"成功"
									});
									this.changemylike()
								}
							})
						} else {

						}
						// this.text = 'request success';
					}
				});
			} else {
				uni.showToast({
					title: "请登陆之后再使用此功能"
				})
			}
		},
		switchType(type) {
			if (this.typeClass == type) {
				return;
			}
			uni.pageScrollTo({
				scrollTop: 0,
				duration: 0
			})
			this.typeClass = type;
			this.subState = this.typeClass == '' ? '' : 'show' + type;
			setTimeout(() => {
				this.oldIndex = null;
				this.theIndex = null;
				this.subState = this.typeClass == 'goods' ? '' : this.subState;
			}, 200)
		},
		//控制左滑删除效果-begin
		touchStart(index, event) {
			//多点触控不触发
			if (event.touches.length > 1) {
				this.isStop = true;
				return;
			}
			this.oldIndex = this.theIndex;
			this.theIndex = null;
			//初始坐标
			this.initXY = [event.touches[0].pageX, event.touches[0].pageY];
		},
		touchMove(index, event) {
			//多点触控不触发
			if (event.touches.length > 1) {
				this.isStop = true;
				return;
			}
			let moveX = event.touches[0].pageX - this.initXY[0];
			let moveY = event.touches[0].pageY - this.initXY[1];

			if (this.isStop || Math.abs(moveX) < 5) {
				return;
			}
			if (Math.abs(moveY) > Math.abs(moveX)) {
				// 竖向滑动-不触发左滑效果
				this.isStop = true;
				return;
			}

			if (moveX < 0) {
				this.theIndex = index;
				this.isStop = true;
			} else if (moveX > 0) {
				if (this.theIndex != null && this.oldIndex == this.theIndex) {
					this.oldIndex = index;
					this.theIndex = null;
					this.isStop = true;
					setTimeout(() => {
						this.oldIndex = null;
					}, 150)
				}
			}
		},

		touchEnd(index, $event) {
			//解除禁止触发状态
			this.isStop = false;
		},

		//删除商品
		deleteCoupon(id, List) {
			let len = List.length;
			for (let i = 0; i < len; i++) {
				if (id == List[i].sp_pid) {
					List.splice(i, 1);
					break;
				}
			};
			uni.setStorage({
				key:"mylike",
				data:List,
				success: res => {
					console.log("成功");
					this.changemylike()
				}
			})
			this.oldIndex = null;
			this.theIndex = null;
		},

		discard() {
			//丢弃
		}


	}
}
